*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.description {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  text-transform: uppercase;
  color: #333333;
}
.main-header {
  display: flex;
  flex-shrink: 0;
  min-height: 56px;
}
.main-nav {
  position: relative;
  display: flex;
  width: 100%;
  min-height: 55px;
}
.main-nav__wrapper {
  display: flex;
  width: 100%;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  width: inherit;
  min-height: 223px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__link {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.44;
  color: #ffffff;
  text-decoration: none;
}
.main-nav__list-item {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 55px;
  font-weight: 700;
  cursor: pointer;
}
.main-nav__logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;
  height: 96px;
  transform: translateX(-50%);
}
.main-nav__toggle {
  position: absolute;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  right: 0;
  width: 63px;
  height: 56px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  display: block;
  width: 24px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 -8px 0 0 #81b3d2;
}
.main-nav--closed .main-nav__toggle:hover::before {
  content: "";
  background-color: rgba(129, 179, 210, 0.6);
  box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.6), 0 -8px 0 0 rgba(129, 179, 210, 0.6);
}
.main-nav--closed .main-nav__toggle:active::before {
  content: "";
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 8px 0 0 rgba(129, 179, 210, 0.3), 0 -8px 0 0 rgba(129, 179, 210, 0.3);
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 32px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    left: 0;
  }
  .main-nav--opened .main-nav__list {
    z-index: 2;
    gap: 1px;
    background-color: #81b3d2;
  }
  .main-nav__list-item:not(:last-child) {
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
  }
  .main-nav__list-item:not(:first-child) {
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
  .main-nav__list-item:last-child::after {
    position: absolute;
    top: 97%;
    content: "";
    width: 100%;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
    mix-blend-mode: normal;
  }
  .main-nav__list-item:hover {
    background-color: #669ec0;
  }
  .main-nav__list-item:active {
    color: rgba(255, 255, 255, 0.3);
    background-color: #5496bd;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
  }
  .main-nav--nojs .main-nav__list {
    z-index: 2;
    background-color: #81b3d2;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    justify-content: space-between;
