.sites__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  margin-bottom: 24px;
}

.sites__title {
  margin-bottom: 20px;
}

.sites__list {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.sites__item {
  min-width: 224px;
}

.sites__checkbox {
  position: relative;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 9%;
}

.sites__input:checked + .sites__checkbox::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 22px;
  height: 16px;
  background: url("../../img/check-tick.svg") no-repeat center / cover;
  content: "";
  transform: translate(-50%, -50%);
}

.sites__checkbox:hover {
  background-color: #e5e5e5;
}

.sites__checkbox:active {
  background-color: #d5d5d5;
}

.sites__checkbox:active.sites__checkbox::before {
  opacity: 0.3;
}

.sites__input:focus-visible + .sites__checkbox {
  border: 2px solid #333333;
  outline-offset: 2px;
}


@media (max-width: @mobile-width-only) {
  .sites__title br {
    display: none;
  }
}

@media (min-width: @tablet-width) {
  .sites .sites__wrapper {
    row-gap: 28px;
    margin-bottom: 42px;
  }

  .sites__list {
    justify-content: space-between;
    column-gap: 32px;
    padding: 0 84px;
  }

  .sites__title {
    position: relative;
    width: 100%;
    margin-bottom: 28px;

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      content: "";
      background-color: #dbdbdb;
      height: 1px;
      width: 123px;
    }

    &::before {
      left: 0;
    }

    &::after {
      right: 0;
    }
  }
}

@media (min-width: @desktop-width) {
  .sites .sites__wrapper {
    margin-bottom: 86px;
  }

  .sites__list {
    column-gap: 64px;
    padding: 0;
  }

  .sites__item {
    min-width: 0;
  }

  .sites__title {
    margin-bottom: 60px;

    &::before,
    &::after {
      width: 280px;
    }
  }
}
